.wrapper {
  height: 100vh;
  background: #307ac2;
  display: grid;
  gap: 20px;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    'header'
    'content';
  overflow: auto;
  position: relative;

  .header {
    grid-area: header;
    position: sticky;
    top: 0;
    z-index: 6;
    background: #307ac2;
  }
}
