.card {
  height: 430px;
  width: 250px;
  margin: 1rem 2rem;
  border-radius: 8px;
  background: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  position: relative;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
    transition: 0.8s;
  }

  .card_image {
    height: 320px;
    width: 100%;
    z-index: 5;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .card_name {
    position: absolute;
    height: 100px;
    bottom: 0.5%;
    width: 100%;
    padding: 0 0.5rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: initial;
    text-align-last: left;
    font-size: 16px;
    color: #343434;
    font-weight: 700;
  }
}
